@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding-bottom: 16px ;
  font-family: 'Montserrat', sans-serif;
  background-color: black;
  color: white;
}

.coin-header{
  margin-bottom: 48px;
  color: #7918f2;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: white;
}

.coin-search {
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;

}

.coin-input{
  padding-left: 16px;
  margin-top: 8px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  color: white;
  background-image: linear-gradient(-225deg, #19141b 0%, #7918f2 48%, #4801ff 100%);
  

}

.coin-input:placeholder {
  color: white
  
}
.coin-container {
    display: flex;
    justify-content: center;
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;

}

.coin h1 {
    font-size: 16px;
    width: 150px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}


.coin-symbol {
    text-transform: uppercase;
}

.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%
}

.coin-price {
    width: 110px;
    padding-right:64px;
}

.coin-volume {
    width: 230px
   
}

.coin-marketcap {
    width: 155px;
}

.coin-percent-red{
    color: #f00606;
    width: 100%;
}

.coin-percent-green{
    color: #11d811;
    width: 100%;
}




